div {
    background-color: cornsilk
   }
   fieldset{
    background-color :whitesmoke
   }
   table {
      font-family: arial, sans-serif;
      border-collapse: collapse;
      width: 55%;
    }
   
    td, th ,tr {
      border: 1px solid plum;
      text-align: left;
      padding: 11px;
    }
   
    tr:nth-child(even) {
      background-color: floralwhite;
    }
    .tab{
      float: left;
    }
    legend{
      color:seagreen;
      text-align: left;
   
      font-size-adjust: bold;
      size: 100px;
    }